<template>
    <v-container>
        <v-card
        height="500px"
        width="380px"
        class="mx-auto my-12"
        elevation="12"
        style=" position: absolute; left: calc(50% - 100px); top: calc(50% - 400px - 500px); z-index:20000"
        :dark="getDarkmode()"
        >

            <v-card-title
            v-if="getDarkmode()"
            style="color:white"
            >
                Erstelle eine neue Gruppe
            </v-card-title>
            <v-card-title
            v-else
            style="color:black"
            >
                Erstelle eine neue Gruppe
            </v-card-title>
            <v-card-text>

                <v-text-field
                v-model="gruppenname"
                label="Gruppenname eingeben"
                :dark="getDarkmode()"
                :counter="50"
                > 
                </v-text-field>

                <v-text-field
                v-model="gruppenbeschreibung"
                label="Gruppenbeschreibung eingeben"
                :counter="100"
                :dark="getDarkmode()"
                > 
                </v-text-field>

                <v-list
                rounded
                class="overflow-y-auto"
                max-height="230px"
                min-height="230px"
                :dark="getDarkmode()"
                >
                    <v-subheader
                    :dark="getDarkmode()"
                    >
                        Wähle Nutzer für deine Gruppe aus
                    </v-subheader>
                    <v-list-item-group
                    v-model="addmodel"
                    :dark="getDarkmode()"
                    color="blue"
                    multiple
                    value="addUsers"
                    >
                        <v-list-item
                        v-for="(item, i) in addingUsers"
                        :key="i"
                        v-bind="attrs"
                        v-on="on"
                        @click="addUser(item.benutzername)"
                        :dark="getDarkmode()"
                        >
                        <v-list-item-avatar>
                                <v-img
                                v-if="item.profilbild === undefined"
                                class="elevation-6"
                                alt=""
                                src="data:image/png;base64,"
                                ></v-img>
                                <v-img
                                v-else
                                :src="item.profilbild.base64"
                                >
                                </v-img>
                        </v-list-item-avatar>
                        <v-list-item-title
                        v-if="getDarkmode()"
                        style="color:white"
                        >
                            {{item.benutzername}}
                        </v-list-item-title>
                        <v-list-item-title
                        v-else
                        style="color:black"
                        >
                            {{item.benutzername}}
                        </v-list-item-title>
                        </v-list-item>
                    </v-list-item-group>
                </v-list>

            </v-card-text>
            <v-card-actions>
                <v-row
                align="space-around"
                >
                    <v-col>
                        <v-btn
                        dark
                        color="red darken-1"
                        @click="closeNewChat();"
                        >
                            Abbrechen
                        </v-btn>
                    </v-col>
                    <v-spacer>
                    </v-spacer>
                    <v-col>
                        <v-btn
                        color="green darken-1"
                        dark
                        @click="createNewGroup()"
                        :disabled="addUsers.length === 0  || !gruppenname"
                        >
                            Absenden
                        </v-btn>
                    </v-col>
                </v-row>
            </v-card-actions>
        </v-card>
    </v-container>
</template>

<script>
import { EventBus } from './EventBus.js';
import { ImageContainer } from './ImageContainer.js';
import axios from "axios";

export default {
    name: 'NewChat',

    data: () => ({
        addingUsers: {},
        addUsers: [],
        gruppenname: '',
    }),

    mounted() {

        console.warn("lengthmounted " + this.addUser.length)        

        EventBus.$on('APP_INTERVAL', (payload) => {
        }); // EventBus.$on('APP_INTERVAL')

        EventBus.$on('NEW_GROUP', (payload) => {
            this.loadData();
        }); // EventBus.$on('APP_INTERVAL')

    },

    methods: {

        getDarkmode(){
            return window.DARK_MODE
        },

        loadData() {
            console.warn("loadData")
            console.log(window.IP_ADDRESS + "/GFOS/daten/nutzer/getAddable/" + "100" + "/" + window.CURRENT_USER_ID + "/" + window.CURRENT_TOKEN)
            axios.get(window.IP_ADDRESS + "/GFOS/daten/nutzer/getAddable/" + "100" + "/" + window.CURRENT_USER_ID + "/" + window.CURRENT_TOKEN)
            .then((resp)=>{
                console.warn("data" + resp.data)
                this.addingUsers = resp.data
                console.warn(this.addingUsers)
            })
            .catch(error =>{
                if(error.response.data === "Kein gültiges Token"){
                    EventBus.$emit("INFOMESSAGE", {"message": "Deine Sitzung ist abgelaufen. Bitte melde dich erneut an!", "icon": "ERROR"})
                    EventBus.$emit("LOGOUT", {})
                }
                
            })
        },

        closeNewChat() {
            EventBus.$emit("CLOSE_NEW_GROUP", {});
            this.gruppenbeschreibung = ""
            this.gruppenname = ""
        },

        addUser(name){
            console.warn(this.gruppenname)
            if(this.addUsers.includes(name)){
                this.addUsers.pop(name)
            }
            else{
                this.addUsers.push(name)
            }
            
            console.warn(this.addUsers);
            console.warn("length " + this.addUsers.length)
            console.warn("isarray " + Array.isArray(this.addUsers))
        },

        createNewGroup() {
            console.log(this.addUsers)
            const now = Date.now();
            const payload = {
                "eigeneId": window.CURRENT_USER_ID,
                "erstelldatum": now,
                "name": this.gruppenname,
                "benutzernamen": this.addUsers
            }
            const headers = {
                                'Accept': 'application/json', // Antwort von Server
                                'Content-Type': 'text/plain', // Wird an den Server gesendet
                            }
            axios.post(window.IP_ADDRESS +"/GFOS/daten/chat/createAsGroup/" + window.CURRENT_TOKEN , payload, { headers})
            .then((resp)=>{
                console.warn(resp)
                EventBus.$emit('INFOMESSAGE', {'message': 'Du hast die Gruppe ' + this.gruppenname + ' erfolgreich erstellt.', 'icon': 'SUCCESS'})
                this.closeNewChat()
            })
            .catch(error =>{
                if(error.response.data === "Kein gültiges Token"){
                    EventBus.$emit("INFOMESSAGE", {"message": "Deine Sitzung ist abgelaufen. Bitte melde dich erneut an!", "icon": "ERROR"})
                    EventBus.$emit("LOGOUT", {})
                }
                
            })
        },
    }
};

</script>