<template>
    <v-dialog
    v-model="dialog"
    max-width="290"
    >
        <v-card
        dark
        color="#202b36"
        max-width="300"
        >
            <v-card-title class="headline">
            Nachrichteninfo
            </v-card-title>

            <v-card-text>
                
                <v-list
                rounded
                color="#202b36"
                class="overflow-y-auto"
                >
                    <v-list-item-group
                    v-model="model"
                    dark
                    color="blue"
                    >
                        <v-list-item
                        v-for="(item, i) in infoData.gelesenVon"
                        :key="i"
                        >

                                <v-list-item-avatar>
                                    <div v-if="item.profilbild === undefined">
                                        <v-img 
                                        src='data:image/png;base64,'
                                        ></v-img>
                                    </div>
                                    <div v-else>
                                        <v-img
                                        :src="item.profilbild.base64"
                                        ></v-img>
                                    </div>
                                </v-list-item-avatar>


                                <v-list-item-content>
                                    <v-list-item-title
                                    v-if="item.benutzername.localeCompare(benutzername) !== 0"
                                    >
                                        {{item.benutzername}}
                                    </v-list-item-title>
                                    <v-list-item-title
                                    v-else
                                    style="font-style:italic"
                                    >
                                        Du
                                    </v-list-item-title>
                                </v-list-item-content>

                                <v-list-item-icon>
                                        <v-img
                                        max-height="32px"
                                        max-width="32px"
                                        src="data:image/png;base64,"
                                        >
                                        </v-img>
                                </v-list-item-icon>

                            
                        </v-list-item>

                    </v-list-item-group>
                </v-list>

                <v-list
                rounded
                color="#202b36"
                class="overflow-y-auto"
                v-if="infoData.nichtgelesenVon.length !== 0"
                >
                    <v-list-item-group
                    v-model="model"
                    dark
                    color="blue"
                    >
                        <v-list-item
                        v-for="(item, i) in infoData.nichtgelesenVon"
                        :key="i"
                        >
                            <v-list-item-avatar>
                            <div v-if="item.profilbild === undefined">
                                <v-img 
                                src='data:image/png;base64,'
                                ></v-img>
                            </div>
                            <div v-else>
                                <v-img
                                :src="item.profilbild.base64"
                                ></v-img>
                            </div>
                        </v-list-item-avatar>


                        <v-list-item-content>
                            <v-list-item-title
                            >
                                {{item.benutzername}}
                            </v-list-item-title>
                        </v-list-item-content>
                        </v-list-item>

                    </v-list-item-group>
                </v-list>

            </v-card-text>

            <v-card-actions>
            <v-spacer></v-spacer>

            <v-btn
                color="darken-1"
                text
                @click="dialog = false"
            >
                Schließen
            </v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>
</template>

<script>
import { EventBus } from './EventBus.js';
import { ImageContainer } from './ImageContainer.js';
import axios from 'axios';

export default {
    data: () => ({
        dialog: false,
        infoData: {},
        benutzername: window.CURRENT_USERNAME
    }),

    mounted() {
        EventBus.$on("OPENMESSAGEINFO", (payload) => {
            console.warn("Nachrichteninfo" + payload["id"]) 
            console.warn(payload["id"])
            this.loadData(payload["id"]);
            this.dialog = true
        });

        EventBus.$on("RELOAD", (payload) =>{
            this.loadData();
        });
    },

    methods: {
        //
        loadData(id) {
            console.warn("loadData" + id)
            console.log(window.IP_ADDRESS +"/GFOS/daten/nachricht/info/" + id + "/" + window.CURRENT_USER_ID + "/" + window.CURRENT_TOKEN)
            axios.get(window.IP_ADDRESS +"/GFOS/daten/nachricht/info/" + id + "/" + window.CURRENT_USER_ID + "/" + window.CURRENT_TOKEN)
            .then((resp)=>{
                console.warn(resp.data)
                this.infoData = resp.data
            })
            .catch(error =>{
                if(error.response.data === "Kein gültiges Token"){
                    EventBus.$emit("INFOMESSAGE", {"message": "Deine Sitzung ist abgelaufen. Bitte melde dich erneut an!", "icon": "ERROR"})
                    EventBus.$emit("LOGOUT", {})
                }
                
            })
        }
    },
}

window.closeInfo = function() {
    EventBus.$emit("CLOSEMESSAGEINFO", {})
}

</script>
