en, wie er möchte. Dabei wird allerdings darauf hingewiesen, dass bei einer sehr hohen Anzahl von Accounts pro Person die persönliche Organisation verkompliziert werden könnte.")]),_c('p',[_vm._v("d. Der Messenger-Dienst “Disputatio” ist kostenlos. Durch die Nutzung von Privatpersonen oder Unternehmen sollen keine Kosten entstehen. Darüber hinaus ist auf der gesamten Plattform des Messenger-Dienstes der gewerbliche Handel mit allen Waren, Dienstleistungen oder Ressourcen verboten.")]),_c('p',{staticStyle:{"font-size":"large"}},[_vm._v("2. Ausschluss")]),_c('p',[_vm._v(" a. Sollten Nutzer auf dem Messenger-Dienst sich nicht nach Ordnungen der allgemeinen Nutzungsbedingungen verhalten oder durch sonstige Verhaltensweisen oder Begebenheiten (strafrechtliche Verfolgung etc. ) auffällig werden, behält sich der Betreiber vor, die jeweiligen Personen von der Nutzung auszuschließen. In diesem Fall entfallen §1 a-c sowie alle nachfolgenden Paragraphen.")]),_c('p',[_vm._v(" b. Der Betreiber behält sich überdies vor, den Messenger-Dienst zu jeder Zeit einzustellen. Somit besteht kein Recht auf die Aushändigung gewisser Inhalte oder die Weiterführung der Dienstleistung. Zur Erklärung wird besonders wird auf §1 c verwiesen.")]),_c('p',{staticStyle:{"font-size":"large","font-weight":"bold"}},[_vm._v("§2: Umgang mit Chatverläufen")]),_c('p',{staticStyle:{"font-size":"large"}},[_vm._v("1. Inhalt von Chatverläufen")]),_c('p',[_vm._v(" a. Der Inhalt der Chatverläufe im Messenger-Dienst “Disputatio” sind privat. Sie sind nur von den Personen, die an der jeweiligen Chatsituation beteiligt sind, zu lesen. Diese Funktion wird zum einen durch den Betreiber sichergestellt, muss aber von den Benutzern ebenso beachtet werden. Ausnahmen zu dieser Regelung finden sich in Absatz")]),_c('p',[_vm._v("b. Jeder Nutzer hat sich im Messenger-Dienst “Disputatio” gemäß gesellschaftlicher Konventionen zu verhalten. Dabei ist darauf zu achten, dass beim Verschicken von Textnachrichten, Bildern, Memes oder Stickern die Freiheit und Würde anderer Chatteilnehmer nicht begrenzt wird. Dieser Umstand wird aufgrund von Absatz 1 vom Betreiber nicht kontrolliert.")]),_c('p',[_vm._v("c. Beide vorrangegangenen Absätze entfallen, sofern durch oder gegen die Inhalte der Chats oder dessen Teilnehmer strafrechtlich ermittelt wird. In diesem Falle behält sich der Betreiber vor, die Inhalte sowie die eingetragenen Nutzerdaten den örtlichen Behörden zu übergeben. Dies wird allerdings nur durch gerichtliche Anordnung wirksam.")]),_c('p',[_vm._v("d. Im Allgemeinen wird für die Benutzung von “Disputatio” darum gebeten, einen friedvollen und respektvollen Umgang mit anderen Nutzern zu pflegen.")]),_c('p',{staticStyle:{"font-size":"large"}},[_vm._v(" 2. Versendung von relevanten Inhalten")]),_c('p',[_vm._v(" a. Der Betreiber des Messenger-Dienstes “Disputatio” übernimmt für den Inhalt von Textnachrichten sowie für den Inhalt von Bildnachrichten (siehe §2, 3) keine Haftung.")]),_c('p',[_vm._v("b. Sollte es zum begründeten Verdacht kommen, dass die Inhalte der Chats einer Person strafrechtlich relevant sein könnten, behält sich der Betreiber vor §2, 1a außer Kraft zu setzen. In diesem Falle können die Inhalte der Chatverläufe an Behörden der jeweiligen Länder weitergeben werden.")]),_c('p',[_vm._v("c. Darüber hinaus wird für die Sicherheit der Inhalte der Textnachrichten keine Haftung übernommen. “Disputatio” schützt die Chats zwar, ist allerdings im Falle von Datenklau o.Ä. nicht für eine Entschädigung heranzuziehen. Bemühungen, dem Datenklau entgegenzuwirken werden vom Betreiber unterstützt.")]),_c('p',{staticStyle:{"font-size":"large"}},[_vm._v("Bildinhalten")]),_c('p',[_vm._v("b. Für den Inhalt der Bildinhalte übernimmt der Messenger keine Haftung. Sofern strafrechtlich relevante Inhalte auf Bildern zu sehen sind oder ein begründeter Verdacht zu dieser Annahme besteht, ist der Messenger bereit, Nutzungsdaten von Chatverläufen und deren Inhalte weiterzugeben.")]),_c('p',[_vm._v(" c. Dar